export default {
	props: {
		item: Object,
		index: Number
	},
	data(){
		return{
			guanzhu:this.item.isguanzhu,
			infonum:this.item.infonum
		}
	},
	methods: {
		//关注操作
		guanzhu1() {
			this.guanzhu = true;
			uni.showToast({
				duration: 2000,
				title: "关注成功"
			})
		},
		//点击ding和cai的操作
		caozuo(type) {
			switch (type) {
				case 'ding':
					// 如果已经是ding则不操作
					if (this.infonum.index == 1) {
						return
					}
					this.infonum.index = 1;
					this.infonum.dingnum++;
					if (this.infonum.index == 1) {
						this.infonum.cainum--;
					}
					break;
				case 'cai':
					// 如果已经是cai则不操作
					if (this.infonum.index == 2) {
						return
					}
					this.infonum.index = 2;
					this.infonum.cainum++;
					if (this.infonum.index == 2) {
						this.infonum.dingnum--;
					}
					break;
				default:
					break;
			}
		}
	}
}
